import React, { Component } from 'react';
import './Coupons.css';
import { Tabs, PullToRefresh, Badge } from 'antd-mobile';
import Coupon from '../components/Coupon';
import Coupon1 from '../components/Coupon1';
import Coupon2 from '../components/Coupon2';
import Loading from '../components/Loading';
import Axios from 'axios';

class Coupons extends Component {
    constructor(props) {
        super(props);
        this.refresh = this.refresh.bind(this);

        this.state = {
            refresh: false,
            hideLoading: false,
            coupons: [
                {
                    name: '优惠券1',
                    imageUrl: 'image/course1.jpg',
                    category: 'C++,Java',
                    starttime: '2018-07-26 00:00:00.0',
                    endtime: '2018-08-10 00:00:00.0',
                    discount: 0.9,
                },{
                    name: '优惠券2',
                    imageUrl: 'image/course1.jpg',
                    category: 'C++,Java',
                    starttime: '2018-07-26 00:00:00.0',
                    endtime: '2018-08-10 00:00:00.0',
                    discount: 0.9,
                }
            ],
            creditproducts: [
            	{
                    name: '积分商城1',
                    imageUrl: 'image/course2.jpg',
                    category: 'C++,Java',
                    duration: 2,
                    discount: 0.9,
                    credit: 10,
               },
               {
                    name: '积分商城2',
                    imageUrl: 'image/course2.jpg',
                    category: 'C++,Java',
                    duration: 2,
                    discount: 0.9,
                    credit: 10,
                }
            ]
        }
    };
    componentWillMount() {
      window.scrollTo(0, 0);
    };
    render() {
        const tabs = [
            { title: <Badge>未使用</Badge> },
            { title: <Badge>已使用</Badge> },
            { title: <Badge>积分商城</Badge> },
        ];
        let coupons =[];
        let creditproducts =[];
        
        let waitingCoupons = [];
        const wCoupons = this.state.coupons.filter(a => {
            return a.valid === 1;
        });
        let paidCoupons = [];
        const pCoupons = this.state.coupons.filter(a => {
            return a.valid === 0;
        });
        
        let creditproduct = [];
        const creditproduct1 = this.state.creditproducts;
        
        //console.log("creditproducts:"+this.state.creditproducts);
        //console.log("creditproduct1:"+creditproduct1);

        for (let i = 0; i < wCoupons.length; i++) {
            waitingCoupons.push(<Coupon key={wCoupons[i].couponid} id={wCoupons[i].couponid} name={wCoupons[i].name}
                category={wCoupons[i].category} starttime={wCoupons[i].starttime}
                imageUrl={wCoupons[i].imageUrl} endtime={wCoupons[i].endtime}
                discount={wCoupons[i].discount}></Coupon>)
        }
        for (let i = 0; i < pCoupons.length; i++) {
            paidCoupons.push(<Coupon1 key={pCoupons[i].couponid} id={pCoupons[i].couponid} name={pCoupons[i].name} 
                category={pCoupons[i].category} starttime={pCoupons[i].starttime}
                imageUrl={pCoupons[i].imageUrl} endtime={pCoupons[i].endtime}
                discount={pCoupons[i].discount}></Coupon1>)
        }
        for (let i = 0; i < creditproduct1.length; i++) {
            creditproduct.push(<Coupon2 key={creditproduct1[i].cpid} id={creditproduct1[i].cpid} name={creditproduct1[i].cpname} 
                category={creditproduct1[i].category} duration={creditproduct1[i].duration}
                imageUrl={creditproduct1[i].imageUrl} credit={creditproduct1[i].credit}
                discount={creditproduct1[i].discount}></Coupon2>)
        }

        return (
            <div className="Coupons">
                <Loading hide={this.state.hideLoading}/>
                <Tabs tabs={tabs}
                initialPage={0}
                tabBarActiveTextColor="#1cb6b3"
                >
                
                <div style={{height: '100%', backgroundColor: '#fff' }}>
                <PullToRefresh refreshing={this.state.refresh} onRefresh={this.refresh}>
                    {waitingCoupons}
                </PullToRefresh>
                </div>
                <div style={{height: '100%', backgroundColor: '#fff' }}>
                <PullToRefresh refreshing={this.state.refresh} onRefresh={this.refresh}>
                    {paidCoupons}
                </PullToRefresh>
                </div>
                <div style={{height: '100%', backgroundColor: '#fff' }}>
                <PullToRefresh refreshing={this.state.refresh} onRefresh={this.refresh}>
                    {creditproduct}
                </PullToRefresh>
                </div>
                
                </Tabs>
            </div>
        );
    };
    componentDidMount() {
        Axios.post("/SSM/test/CustomerHandler_islogin")
        .then(res => {
        if (res.data.result) {
            this.setState({hideLoading: true});
        } else {
            window.location.hash = 'login';
        }
        })
        .catch(err => {
        alert(err);
        // window.location.assign("../#login");
        });

        Axios.post("/SSM/test/CouponHandler_findCouponByCid")
        .then(res => {
            if (res.data) {
                this.setState({
                    hideLoading: true,
                    coupons: res.data.map(item => {
                        return {
                        	couponid : item.couponid,
                            name: item.name,
                            category: item.category,
                            imageUrl: 'image/coupon.jpg',
                            starttime: item.starttime,
                            endtime: item.endtime,
                            discount: item.discount,
                            valid: item.valid,
                        }
                    })
                })
            }
        })
        .catch(err => {
            alert(err);
        });
        
        Axios.post("/SSM/test/CouponHandler_findCreditproduct")
        .then(res => {
            if (res.data) {
                this.setState({
                    hideLoading: true,
                    creditproducts: res.data.map(item => {
                        return {
                        	cpid : item.cpid,
                            credit: item.credit,
                            discount: item.discount,
                            duration: item.duration,
                            imageUrl: 'image/coupon.jpg',
                            category: item.category,
                            cpname: item.cpname,
                        }
                    })
                })
            }
        })
        .catch(err => {
            alert(err);
        });

        // setTimeout(() =>
        //     this.setState({hideLoading: true}),
        //     1000
        // );
    };
    refresh() {
        this.setState({refresh: true});

        Axios.post("/SSM/test/CouponHandler_findCouponByCid")
        .then(res => {
            if (res.data) {
                this.setState({
                    hideLoading: true,
                    coupons: res.data.map(item => {
                        return {
                        	couponid : item.couponid,
                            name: item.name,
                            category: item.category,
                            imageUrl: 'image/coupon.jpg',
                            starttime: item.starttime,
                            endtime: item.endtime,
                            discount: item.discount,
                            valid: item.valid,
                        }
                    })
                })
            }
        })
        .catch(err => {
            alert(err);
        });
        
        Axios.post("/SSM/test/CouponHandler_findCreditproduct")
        .then(res => {
            if (res.data) {
                this.setState({
                    hideLoading: true,
                    creditproducts: res.data.map(item => {
                        return {
                        	cpid : item.cpid,
                            credit: item.credit,
                            discount: item.discount,
                            duration: item.duration,
                            imageUrl: 'image/coupon.jpg',
                            category: item.category,
                            cpname: item.cpname,
                        }
                    })
                })
            }
        })
        .catch(err => {
            alert(err);
        });

        setTimeout(() => {
            this.setState({refresh: false});
        }, 1000);
    };
}

export default Coupons;



