import React, { Component } from 'react';
import './CourseList.css';
import {Card, Image} from 'semantic-ui-react'
import { NavLink } from 'react-router-dom'

class CoursesList extends Component {
  constructor(props) {
      super(props);

      this.state = {};
  };
  render() {
    let list = [];
    
		console.log("couponid:"+this.props.couponid);
		console.log("category:"+this.props.category);
		
    if (Array.isArray(this.props.list)) {
    	if(this.props.couponid && this.props.category){  //使用优惠券购买
    		for (let item of this.props.list) {
            	if(item.type===1){  //fid
            		list.push(<Card className="course-card" key={item.courseId}>
                    <NavLink to={"/coursedetail?fid=" + item.courseId}>
                        <Image className="course-image" src={item.imageUrl}/>
                    </NavLink>
                    <Card.Content>
                    <Card.Header>{item.courseTitle}</Card.Header>
                    <Card.Meta>
                        <span>￥{item.price}</span>
                    </Card.Meta>
                    </Card.Content>
                </Card>
                );
            	}else{  //lid
            		list.push(<Card className="course-card" key={item.courseId}>
                    <NavLink to={"/coursedetail?lid=" + item.courseId +"&couponid="+this.props.couponid+"&category="+this.props.category}>
                        <Image className="course-image" src={item.imageUrl}/>
                    </NavLink>
                    <Card.Content>
                    <Card.Header>{item.courseTitle}</Card.Header>
                    <Card.Meta>
                        <span>￥{item.price}</span>
                    </Card.Meta>
                    </Card.Content>
                </Card>
              );
           } 
        }
    	}else{  //normal
    		for (let item of this.props.list) {
    			
            list.push(
                <Card className="course-card" key={item.courseId}>
                    <NavLink to={"/coursedetail?" + ((item.type === 1) ? "fid=" : "lid=") + item.courseId}>
                        <Image className="course-image" src={item.imageUrl}/>
                    </NavLink>
                    <Card.Content>
                    <Card.Header>{item.courseTitle}</Card.Header>
                    <Card.Meta>
                        <span>￥{item.price}</span>
                    </Card.Meta>
                    </Card.Content>
                </Card>
            );
            
        }
    	} 
    }
    return (
      <div className="CoursesList">
        {list}
      </div>
    );
  }
}

export default CoursesList;


