import React, { Component } from 'react';
import './Coupon.css';
import { Button, Modal } from 'antd-mobile';
import { NavLink } from 'react-router-dom';
import Axios from 'axios';

class Coupon extends Component {
    constructor(props) {
        super(props);
        this.exchange = this.exchange.bind(this);
        this.use = this.use.bind(this);

        this.state = {
            couponButton: [
                <div className="coupon-footer"><Button className="coupon-button" size="small" onClick={this.exchange}>兑换</Button></div>,
                <div className="coupon-footer"><Button className="coupon-button" size="small" onClick={this.use}>使用</Button></div>,
                <div className="coupon-footer"></div>
            ]
        }
    };
    render() {
        return (
            <div className="Coupon">
                <div className="coupon-header">
                    <span>{this.props.name}</span>
                </div>
                <div className="coupon-body">
                    <img className="coupon-img" src={this.props.imageUrl} alt="图片"/>
                    <div className="coupon-detail">
                        <div className="coupon-category">
                            适用类型:{this.props.category}
                        </div>
                        <div className="coupon-discount">
                            折扣:{this.props.discount}
                        </div>
                        <div className="coupon-time">
                            有效期:{this.props.starttime}--{this.props.endtime}
                        </div>
                    </div>
                </div>
                {this.state.couponButton[1]}
            </div>      
        );
    };
     exchange(){
    	Modal.alert('兑换', '兑换将消耗积分，是否确认兑换？？', [
            { text: '取消', onPress: () => {}, style: 'default' },
            { text: '确认', onPress: () => {
                let param = new URLSearchParams();
                param.append('cpid', this.props.id);
                Axios.post("/SSM/test/CouponHandler_exchangeCreditproduct", param)
                .then(() => {
                    //nothing to do
                })
                .catch(err => {
                    alert("兑换失败");
                })
            }}]);
    };
    use(){
    	Modal.alert('使用', '是否确认使用此优惠券？？', [
            { text: '取消', onPress: () => {}, style: 'default' },
            { text: '确认', onPress: () => {
	        	window.location="/build/index.html#/courses?couponid="+ this.props.id +"&category="+this.props.category;
            }}]);
    };
}

export default Coupon;


